@font-face {
  font-family: Avenir-Book;
  src: url(fonts/Avenir-Book.ttf);
}

@font-face {
  font-family: Avenir-Heavy;
  src: url(fonts/Avenir-Heavy.ttf);
}

form {
  position: relative;
  margin-bottom: 20px;
}

.search_one {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0px 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;

  height: 66px;
  width: 100%;
  padding-left: 62px;

  font-family: "Avenir";
  font-size: 16px;
  line-height: 22px;
  color: #3d4e61;
}

.search_one:hover {
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0px 3px 8px rgba(61, 78, 97, 0.2);
  border-radius: 4px;
}

.search_one:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
}

.image_one {
  display: block;
  position: absolute;
  left: 26px;
  top: 25px;
}

.search_two {
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0px 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;

  height: 38px;
  width: 100%;
  padding-left: 33px;

  font-family: "Avenir";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #3d4e61;
}

.search_two:hover {
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0px 3px 8px rgba(61, 78, 97, 0.2);
  border-radius: 4px;
}

.search_two:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
}

.image_two {
  display: block;
  position: absolute;
  left: 13px;
  top: 15px;
  width: 11px;
  }
